
/* BANNER */

.banner {
    background: linear-gradient(rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.6)), url('../../images/banner.jpg') no-repeat center;
    background-size: cover;
    width: 100%;
    height: 420px;
    position: relative;
}

.banner .row {
    flex-direction: column;
}

.banner_img {
    width: 100%;
    text-align: center;
    margin-top: 40px;
}

.banner_img img {
    width: 250px;
    height: auto;
    margin-top: 50px;

}

.banner_text {
    width: 50%;
    text-align: center;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 10px;
    color: rgba(255, 255, 255, 0.912);
    font-size: 13px;
    line-height: 25px;
}

.banner_buttons p {
    width: 130px;
    height: auto;
    font-size: 17px;
    margin: 10px auto;
    border: 1px solid rgb(256, 256, 256);
    border-radius: 15px;
    padding: 5px;
    text-align: center;
    color: rgba(255, 255, 255, 0.892);
}

.banner_buttons p:hover {
    background-color: rgba(0, 136, 169, 0.501);
    color: rgb(255, 255, 255);
    transition: all 0.3s ease 0s;
    background-color: rgb(153, 3, 3);
}
/* VIEWMAX INTRO */
.viewmax_intro {
    width: 100%;
    height: auto;
    background-color: transparent;
    margin: 20px 0px 20px 0px;
    padding: 25px 25px;

}

.viewmax_intro .row {
    align-items: center;
}

.viewmax_intro_content_1 {
    width: 40%;
    text-align: right;
    font-size: 30px;
    line-height: 40px;
    color: rgb(19, 15, 15);
}

.viewmax_intro_content_2 {
    width: 40%;
    text-align: justify;
    font-size: 20px;
    line-height: 30px;
    color: rgb(53, 51, 51);

}

/* ACCESS MOVIES ANYWHHERE */
.access_movies_anywhere {
    width: 100%;
    height: auto;
    background-color: rgba(218, 218, 218, 0.46);
    margin: 20px 0px 20px 0px;
    padding: 25px;
}

.access_movies_anywhere .row {
    align-items: center;
}

.access_movies_anywhere_content_1 {
    width: 40%;
    margin-left: auto;
    text-align: center;
    /* text align works for images */
}

.access_movies_anywhere_content_1 img {
    width: 250px;
    height: 250px;
    border-radius: 10px;

}

.access_movies_anywhere_content_2 {
    flex-basis: 40%;
    text-align: justify;
    margin-right: auto;
    font-size: 20px;
    line-height: 25px;
    padding: 10px 0px 10px 30px;
}

.access_movies_anywhere_content_2 h1 {
    color: rgb(19, 15, 15);
    text-align: left;
}

.access_movies_anywhere_content_2 p {
    padding-top: 15px;
    color: rgb(53, 51, 51);
}

