/* ONBOARD CINEMA */
.onboard_cinema {
    width: 100%;
    height: auto;
    background-color: transparent;
    margin: 20px 0px 20px 0px;
    padding: 25px;
}

.onboard_cinema .row {
    align-items: center;
}

.onboard_cinema_icon {
    width: 50px;
    height: auto;
}

.onboard_cinema_icon_arrow {
    width: 35px;
    height: auto;
}

.onboard_cinema_content_1 {
    font-size: 27px;
    /* heading text */
}

.onboard_cinema_content_2,
.onboard_cinema_content_3,
.onboard_cinema_content_4 {
    font-size: 16px;
    flex-basis: 25%;
    text-align: center;
    margin: 20px 0px 20px 0px;
    line-height: 20px;
}

/* REQUIREMENTS */
.requirements {
    width: 100%;
    height: auto;
    background-color: rgba(218, 218, 218, 0.46);
    margin: 20px 0px 20px 0px;
    padding: 25px;
}

.requirements_content_1 {
    margin-right: auto;
    text-align: left;
    font-size: 20px;
    line-height: 25px;
}

.requirements_content_1 h1 {
    color: rgb(19, 15, 15);
}

.requirements_content_1 p {
    padding-top: 15px;
    color: rgb(53, 51, 51);
}


/* EXPLORE */
.explore {
    width: 100%;
    height: auto;
    margin: 20px 0px 20px 0px;
    padding: 25px;
    color: rgb(256, 256, 256);
    position: relative;
    /* background: linear-gradient(90deg, rgba(0,0,0,1),rgba(0,0,0,1),rgba(0,0,0,0.5), rgba(0,0,0,0.4)),url('images/banner.jpg') no-repeat center; */
    background: linear-gradient(90deg, rgba(0, 0, 0, 1), rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.4)), url('../../images/explore.jpg') no-repeat center fixed;
    background-size: cover;

}

.explore .row {
    flex-direction: column;
    justify-content: center;
}

.explore_content {
    width: 70%;
}

.explore h1 {
    font-size: 35px;
    padding-left: 5%;
    margin-bottom: 30px;
    color: rgb(228, 34, 34);

}

.explore_content h3 {
    margin-left: 5%;
    color: rgba(255, 255, 255, 0.857);
    font-size: 16px;
    line-height: 25px;
    margin-bottom: 20px;
}

.explore_content p {
    width: 110px;
    color: rgb(235, 233, 233);
    font-size: 17px;
    border-radius: 5px;
    margin-bottom: 20px;
    margin-top: 25px;
    margin-left: 5%;
    padding: 5px;
    text-align: center;
    transition: all 0.5s ease 0s;
    /* background-color: rgba(0, 136, 169, 0.816); */
    /* background-color: rgba(204,51,51,1); */
    background-color: rgb(153, 3, 3);


}

.explore_content p:hover {
    background-color: rgb(114, 3, 3);

}

/* GET CINEMA LICENSE */
.get_cinema_license {
    width: 100%;
    height: auto;
    background-color: transparent;
    margin: 20px 0px 20px 0px;
    padding: 40px 10px 25px 10px;
}

.get_cinema_license .row {
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.get_cinema_license_content_1 {
    /* get viewmax today */
    text-align: center;
    font-size: 30px;
    color: rgba(31, 26, 26, 0.878);
    font-family: Sans-serif, -apple-system, system, sans-serif;
}

.get_cinema_license_content_2 p {
    /* Get Viewmax Cinema License */
    text-align: center;
    font-size: 20px;
    background-color: rgba(114, 3, 3, 0.9);
    color: #fff;
    padding: 5px 10px 5px 10px;
    border-radius: 5px;
    margin: 15px 0px 20px 0px;
    transition: all 0.3s ease 0s;
    cursor: pointer;
}

.get_cinema_license_content_2 p:hover {
    background-color: rgb(114, 3, 3);


}

.get_cinema_license_content_3 {
    /* Available across Africa */
    text-align: center;
    font-size: 14px;
    color: #fff;
    border-radius: 5px;
    color: rgb(53, 51, 51);
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.get_cinema_license_content_4 {
    text-align: end;
    font-size: 12px;
    color: #fff;
    border-radius: 5px;
    color: rgb(53, 51, 51);
    margin: 10px 0px 0px 0px;
    align-self: flex-end;
}

/* TESTIMONY */
.testimony {
    width: 100%;
    height: auto;
    background-color: rgba(218, 218, 218, 0.46);
    margin: 20px 0px 20px 0px;
    padding: 30px 20px 20px 20px;
}

.testimony .row {
    justify-content: space-evenly;
}

.testimony_content_1,
.testimony_content_2 {
    width: 350px;
    background-color: rgba(168, 168, 168, 0.4);
    height: auto;
    padding: 20px 30px 20px 30px;
    margin: 10px;
    position: relative;
}

.testimony img {
    border-radius: 50%;
    width: 90px;
    height: 100px;
    background-color: transparent;
    /* border: 2px rgb(114, 3, 3) solid; */
    position: absolute;
    margin-top: -70px;
    margin-left: -75px;
}


.testimony_content_1 p,
.testimony_content_2 p {
    font-size: 18px;
    line-height: 23px;
}

.testimony #testimony_names {
    text-align: end;
    margin-top: 10px;
}

.testimony #testimony_country {
    text-align: end;
    font-size: 11px;
}




@media only screen and (max-width:768px) {
    .row {
        display: flex;
        flex-direction: column;
    }

    /* BANNER */
    .banner_text {
        width: 80%;
        font-size: 10px;
    }

    /* VIEWMAX INTRO */
    .viewmax_intro {
        background-color: transparent;
    }

    .viewmax_intro_content_1 {
        width: 100%;
        font-size: 23px;
        padding: 0px 0px 0px 0px;
        line-height: 25px;
        text-align: left;
    }

    .viewmax_intro_content_2 {
        width: 100%;
        margin-top: 10px;
        font-size: 15px;
        line-height: 18px;
        padding: 0px 0px 0px 0px;
    }

    /* ACESS MOVIES */
    /* img */
    .access_movies_anywhere_content_1 {
        width: 100%;
        padding: 0px 0px 0px 0px;
        margin: 0px 0px 0px 0px;
    }

    /* text */
    .access_movies_anywhere_content_2 {
        width: 100%;
        font-size: 15px;
        padding: 0px 0px 0px 0px;
        margin: 20px 0px 0px 0px;
        line-height: 20px;
        text-align: left;

    }



    /* EASY SYSTEM SETUP */
    .easy_system_setup {
        background-color: transparent;
    }

    /* text */
    .easy_system_setup_content_1 {
        flex-basis: 100%;
        font-size: 15px;
        padding: 0px 0px 0px 0px;
        line-height: 20px;
        text-align: left;

    }

    /* img */
    .easy_system_setup_content_2 {
        flex-basis: 100%;
        padding: 0px 0px 0px 0px;
        margin: 20px 0px 0px 0px;
    }



    /* IN APP EXPERIENCE */
    /* img */
    .in_app_experience_content_1 {
        flex-basis: 100%;
        margin: 0px 0px 0px 0px;
        padding: 0px 0px 0px 0px;
    }

    /* text */
    .in_app_experience_content_2 {
        flex-basis: 100%;
        font-size: 15px;
        padding: 0px 0px 0px 0px;
        margin: 20px 0px 0px 0px;
        line-height: 20px;
        text-align: left;
    }

    /* ONBOARD CINEMA */
    .onboard_cinema {
        background-color: transparent;
    }


    .onboard_cinema_icon_arrow {
        transform: rotate(90deg);
    }

    .requirements_content_1 {
        flex-basis: 100%;
        font-size: 15px;
        padding: 0px 0px 0px 0px;
    }


    /* GET CINEMA LICENSE */
    .get_cinema_license {
        background-color: transparent;
    }

    .get_cinema_license_content_4 {
        font-size: 8px;
    }

    /* TESTIMONY */
    .testimony_content_1,
    .testimony_content_2 {
        width: 90%;
        margin: 10px auto 20px auto;
        padding: 20px 15px 15px 15px;
        border-radius: 7px;
    }

    .testimony img {
        border-radius: 50%;
        width: 90px;
        height: 100px;
        background-color: transparent;
        /* border: 2px rgb(114, 3, 3) solid; */
        position: absolute;
        margin-top: -70px;
        margin-left: -60px;
    }


    .testimony_content_1 p,
    .testimony_content_2 p {
        font-size: 15px;
    }

    .testimony #testimony_names {
        margin-top: 10px;
    }

}